body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

.lbl {
  position: relative;
  display: block;
  height: 20px;
  width: 44px;
  background: #9b9b9b;
  border-radius: 100px;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.lbl:after {
  position: absolute;
  left: -2px;
  top: -3px;
  display: block;
  width: 26px;
  height: 26px;
  border-radius: 100px;
  background: #dedede;
  -webkit-box-shadow: 0px 3px 3px rgba(0,0,0,0.05);
          box-shadow: 0px 3px 3px rgba(0,0,0,0.05);
  content: '';
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.lbl:active:after { -webkit-transform: scale(1.15, 0.85); -ms-transform: scale(1.15, 0.85); transform: scale(1.15, 0.85); }

.cbx:checked ~ label { background: #005e8B; }

.cbx:checked ~ label:after {
  left: 20px;
  background: #0096dd;
}

.cbx:disabled ~ label {
  background: #d5d5d5;
  pointer-events: none;
}

.cbx:disabled ~ label:after { background: #bcbdbc; }

.hidden { display: none; }


